#light footer {
    background: var(--second-color);
}

footer {
    background: var(--color-bg);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-light);
}

.footer_logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer_socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer_socials a {
    background: var(--color-light);
    color: var(--color-bg);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}
#light .footer_socials a:hover {
    border-color: var(--color-white);
}
.footer_socials a:hover {
    background: transparent;
    color: var(--color-white);
    border-color: var(--color-bg-variant);
}
.link {
    color: var(--color-light);
}
.link:hover {
    color: var(--color-white);
}
.footer_copyright {
    margin-bottom: 4rem;
    color: var(--color-white);
}
.footer_logo:hover {
    color: var(--color-white);
}

/* =========== MEDIA QUERIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer_socials {
        margin-bottom: 2.6rem;
    }
}